require('normalize.css');
require('styles/App.css');

import React from 'react'
import OptionItem from './Highlight'

// let OptionText = React.createClass({
let OptionText = React.createClass({


  getInitialState: function () {
    return {
      condition: false
    }
  },

  change: function () {
    this.setState({ condition : !this.state.condition })
  },

  // highlightSingleHandler: function () {
  //   this.setState({ condition : !this.state.condition })
  // },

  render: function () {
  	for (var i in this.props.options) {
  		this.props.options[i].condition = false;
  	}
    return (
      <ul className='optionAll' >
	    {this.props.options.map((option) =>
	        <OptionItem className="option" key={option.text} texts={option.text} image={option.image} order={option.order} condition={option.condition}/>
	    )}
	  </ul>
    )
    
  }

})

export default OptionText


