require('normalize.css');
require('styles/App.css');

import React from 'react'
import QuestionText from './QuestionText'
import OptionText from './OptionText'
import PageContainer from './transitions'


let ChoiceCard = ({currentQA, handleOptionClick, nextClickHandler, questionNumber, totalQuestionNumber}) =>
  <PageContainer>
	  <div className={currentQA.layout}>
	  	<div className='surface'>
	  		<QuestionText question={(questionNumber + 1) + '/' + totalQuestionNumber + ' ' + currentQA.question}/>
	  		<OptionText options={currentQA.options} handleOptionClick={handleOptionClick}/>
	  		<div className="nextButton" onClick={nextClickHandler}>下一題</div>
	  		<div className="allBar"></div>
	  		<div className="partBar" style={{width: (questionNumber/totalQuestionNumber).toFixed(2)* 100 + '%'}}></div>
	  	</div>
	  </div>
  </PageContainer>

export default ChoiceCard


