require('normalize.css');
require('styles/App.css');

import React from 'react'
import PageContainer from './transitions'

let coverImage = require('../images/coverImage.png')
let startButton = require('../images/startButton.png')


let CoverCard = ({coverClickHandler}) =>
<PageContainer>
  <div className='surface'>
	  <img className="coverImage" src={coverImage}/>
	  <div className="startButton" onClick={coverClickHandler}><img src={startButton}/></div>
  </div>
</PageContainer>

export default CoverCard


