/**
 * A compoent with an array of social sharing buttons
 */

require('normalize.css')
require('styles/App.css')

import React from 'react'
import PageContainer from './transitions'

let OptionItem = React.createClass({


  getInitialState: function () {
    return {
      condition: false
    }
  },

  highlightHandler: function () {
    this.setState({ condition : !this.state.condition })
  },


  render: function () {
    var startStyles = {
        'opacity': 0
    }

    var endStyles = {
        'opacity': 1
    }

    var duration = 500
    var easing = 'easeInQuart'
    return (
      <PageContainer startStyles={startStyles}
                      endStyles={endStyles}
                      duration={duration}
                      easing={easing}>
        <li id='code' width='400px' onClick={this.highlightHandler} className={this.state.condition ? 'highlight' :''}>
          {this.props.order}.
          <img className="optionIcon" src={this.props.image}/>
          &nbsp;&nbsp;{this.props.texts}
        </li>
      </PageContainer>
    )
  }

})

export default OptionItem



